body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body {
  margin: 0;
}

body {
  font-family: 'Roboto', Arial, sans-serif;
  transition: all 0.7s ease-out;
}

#app div {
  display: flex;
  flex-direction: column;
  align-items: center;
}

main {
  height: 100vh;
  overflow: hidden;
  font-size: 0;
}

.frame-wrapper {
  position: relative;
  overflow: hidden;
  display: inline-block;
}

.video-box {
  display: inline-block;
}

.frame-wrapper::before {
  content: '';
  position: absolute;
  top: -10px;
  left: 0;
  right: 0;
  height: 10px;
  box-shadow: 5px 12px 25px rgba(0, 0, 0, 0.6);
}

.frame-wrapper::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 0;
  right: 0;
  height: 10px;
  box-shadow: 5px -12px 25px rgba(0, 0, 0, 0.6);
}

.frame {
  height: 100%;
  width: 100%;
  border: none;
}

.video-wrapper {
  height: 100%;
  width: 100%;
}

video {
	width: 100%;
  height: 100%;
  object-fit: cover;
}

